"use client"

import { FormEvent, useState } from "react"
import { X } from "lucide-react"
import { sendQuoteRequest } from "@/lib/send-quote"

interface QuoteModalProps {
  open: boolean
  onClose: () => void
  source?: string | null
}

const initialForm = {
  name: "",
  email: "",
  phone: "",
  message: "",
}

export default function QuoteModal({ open, onClose, source }: QuoteModalProps) {
  const [formData, setFormData] = useState(initialForm)
  const [status, setStatus] = useState<"idle" | "loading" | "success" | "error">("idle")
  const [errorMessage, setErrorMessage] = useState<string | null>(null)

  if (!open) {
    return null
  }

  const handleSubmit = async (event: FormEvent<HTMLFormElement>) => {
    event.preventDefault()
    setStatus("loading")
    setErrorMessage(null)

    try {
      await sendQuoteRequest({ ...formData, source })
      setStatus("success")
      setFormData(initialForm)
    } catch (error) {
      setStatus("error")
      setErrorMessage(error instanceof Error ? error.message : "Unexpected error")
    }
  }

  const closeModal = () => {
    setStatus("idle")
    setErrorMessage(null)
    onClose()
  }

  return (
    <div className="fixed inset-0 z-[9999] flex items-center justify-center bg-black/70 px-4">
      <div className="relative w-full max-w-lg rounded-3xl bg-white p-8 shadow-2xl">
        <button
          onClick={closeModal}
          className="absolute right-4 top-4 text-slate-500 hover:text-slate-900"
          aria-label="Close quote form"
        >
          <X className="h-5 w-5" />
        </button>
        <p className="text-xs uppercase tracking-[0.4em] text-blue-500 mb-2">Get A Quote Now</p>
        <h2 className="text-3xl font-bold text-slate-900 mb-2">Tell us about your project</h2>
        {source && <p className="text-sm text-slate-500 mb-4">Triggered from: {source}</p>}
        <form onSubmit={handleSubmit} className="space-y-4">
          <div>
            <label className="text-sm font-semibold text-slate-700">Full Name</label>
            <input
              className="mt-1 w-full rounded-xl border border-slate-200 px-4 py-3 focus:border-blue-500 focus:outline-none"
              value={formData.name}
              onChange={(e) => setFormData({ ...formData, name: e.target.value })}
              required
              placeholder="Your name"
            />
          </div>
          <div className="grid gap-4 md:grid-cols-2">
            <div>
              <label className="text-sm font-semibold text-slate-700">Email</label>
              <input
                type="email"
                className="mt-1 w-full rounded-xl border border-slate-200 px-4 py-3 focus:border-blue-500 focus:outline-none"
                value={formData.email}
                onChange={(e) => setFormData({ ...formData, email: e.target.value })}
                required
                placeholder="you@email.com"
              />
            </div>
            <div>
              <label className="text-sm font-semibold text-slate-700">Phone</label>
              <input
                className="mt-1 w-full rounded-xl border border-slate-200 px-4 py-3 focus:border-blue-500 focus:outline-none"
                value={formData.phone}
                onChange={(e) => setFormData({ ...formData, phone: e.target.value })}
                placeholder="15679222017"
              />
            </div>
          </div>
          <div>
            <label className="text-sm font-semibold text-slate-700">Message</label>
            <textarea
              className="mt-1 w-full rounded-2xl border border-slate-200 px-4 py-3 focus:border-blue-500 focus:outline-none"
              rows={4}
              value={formData.message}
              onChange={(e) => setFormData({ ...formData, message: e.target.value })}
              required
              placeholder="Project requirements, specs, quantity..."
            />
          </div>
          {status === "success" && (
            <p className="rounded-2xl bg-green-50 px-4 py-3 text-sm text-green-700">
              Thanks! Your request was sent to 1011300569@qq.com. We will be in touch shortly.
            </p>
          )}
          {status === "error" && (
            <p className="rounded-2xl bg-red-50 px-4 py-3 text-sm text-red-700">{errorMessage}</p>
          )}
          <button
            type="submit"
            disabled={status === "loading"}
            className="w-full rounded-2xl bg-gradient-to-r from-blue-600 to-slate-900 py-3 font-semibold text-white disabled:opacity-60"
          >
            {status === "loading" ? "Sending..." : "Send quote"}
          </button>
          <p className="text-center text-xs text-slate-500">
            The request will be delivered to 1011300569@qq.com and our team will reply within 24h.
          </p>
        </form>
      </div>
    </div>
  )
}
